    .product.box.box-success {
        border-radius: 0px;
    }
    .product .item .img {
        object-fit: cover;
    }
    .product .item .name {
        font-size: 15px;
        margin: 5px 5px 5px 0px;
        color: black;
        height: 33px;
        overflow: hidden;
    }
    .product .item .price {
        font-family: 'Gotu', sans-serif;
    }
    #store-product .pagination{
        margin-top: 0px;
        display: flex;
        justify-content: center;
    }
@media(max-width: 375px) {
    #store-product .user-product.item {
        
    }
    #store-product .img{
        height: 100px;
    }
    #store-product .name{
        font-size: 12px;
        height: 27px;
        margin-bottom: 0px;
    }
    #store-product .price{
        font-size: 12px;
    }
}
</style>